<template>
<example-main-attack :my-list="list">
  <template #attacker>
    <p class="font-bold text-white font-black w-full">مهاجمان تیم بایرن مونیخ</p>
  </template>
</example-main-attack>
</template>

<script>
import ExampleMainAttack from "@/components/main/ExampleMainAttack";
import {onMounted, reactive} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "MainAttack",
  components: {ExampleMainAttack},
  setup(){
    const list=reactive([])
    const sum=onMounted(async ()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/190'),
         axios.get('https://api.footballi.net/api/v2/player/817'),
         axios.get('https://api.footballi.net/api/v2/player/5272'),
         axios.get('https://api.footballi.net/api/v2/player/5648'),
         axios.get('https://api.footballi.net/api/v2/player/2226878'),
         axios.get('https://api.footballi.net/api/v2/player/2236131'),
         axios.get('https://api.footballi.net/api/v2/player/2444642'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
        handleError(error)
          })

    })
    return{
      list,sum
    }
  }
}
</script>

