<template>
<example-defender :my-list="list">
  <template #defender>
    <p class="font-bold text-white font-black w-full">مدافعان تیم بایرن مونیخ</p>
  </template>
</example-defender>
</template>

<script>
import ExampleDefender from "@/components/main/ExampleDefender";
import {onMounted, reactive} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "DefenderBayern",
  components: {ExampleDefender},
  setup(){
    const list=reactive([])
    const result=onMounted(async ()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/235'),
         axios.get('https://api.footballi.net/api/v2/player/2956'),
         axios.get('https://api.footballi.net/api/v2/player/4079'),
         axios.get('https://api.footballi.net/api/v2/player/29008'),
         axios.get('https://api.footballi.net/api/v2/player/43905'),
         axios.get('https://api.footballi.net/api/v2/player/2225902'),
         axios.get('https://api.footballi.net/api/v2/player/2229049'),
         axios.get('https://api.footballi.net/api/v2/player/2231016'),
         axios.get('https://api.footballi.net/api/v2/player/2236488'),
         axios.get('https://api.footballi.net/api/v2/player/2258120'),
      ])
          .then(axios.spread((...response)=>{
            list.push(response)
          }))
          .catch((error)=>{
           handleError(error)
          })

    })
    return{
      list,result,
    }
  }
}
</script>

