<template>
<example-transfer :my-list="menu">

</example-transfer>
</template>

<script>
import {reactive,onMounted} from "vue";
import axios from "axios";
import ExampleTransfer from "@/components/main/ExampleTransfer";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "TransferTeamBayern",
  components: {ExampleTransfer},
  setup(){
    const menu=reactive([])
    const finish=onMounted(async ()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/966'),
         axios.get('https://api.footballi.net/api/v2/player/2956'),
         axios.get('https://api.footballi.net/api/v2/player/1476'),
         axios.get('https://api.footballi.net/api/v2/player/2138'),
         axios.get('https://api.footballi.net/api/v2/player/190'),
         axios.get('https://api.footballi.net/api/v2/player/44577'),
         axios.get('https://api.footballi.net/api/v2/player/1567'),
         axios.get('https://api.footballi.net/api/v2/player/2222702'),
         axios.get('https://api.footballi.net/api/v2/player/2300856'),
         axios.get('https://api.footballi.net/api/v2/player/6194'),
         axios.get('https://api.footballi.net/api/v2/player/2222702'),
         axios.get('https://api.footballi.net/api/v2/player/808'),
         axios.get('https://api.footballi.net/api/v2/player/2225902'),
         axios.get('https://api.footballi.net/api/v2/player/2226878'),
      ])
          .then(axios.spread((...response)=>{
            menu.push(response)
          }))
          .catch((error)=>{
           handleError(error)
          })
    })


    return{
      menu,finish,
    }
  }
}
</script>

