<template>
<example-attack :my-list="list">
  <template #Midfielder>
    <p class="font-bold text-white font-black w-full">هافبک های تیم بایرن مونیخ</p>
  </template>
</example-attack>
</template>

<script>
import ExampleAttack from "@/components/main/ExampleAttack";
import {onMounted, reactive} from "vue";
import axios from "axios";
import {handleError} from "@/components/composable/handleError";

export default {
  name: "AttackBayern",
  components: {ExampleAttack},
  setup(){
    const list=reactive([]);
    const total=onMounted(async ()=>{
     await axios.all([
         axios.get('https://api.footballi.net/api/v2/player/5234'),
         axios.get('https://api.footballi.net/api/v2/player/13164'),
         axios.get('https://api.footballi.net/api/v2/player/2341782'),
         axios.get('https://api.footballi.net/api/v2/player/2364719'),
         axios.get('https://api.footballi.net/api/v2/player/2405085'),
         axios.get('https://api.footballi.net/api/v2/player/2447296'),
      ])
          .then(axios.spread((...res)=>{
            list.push(res)
          }))
          .catch((error)=>{
            handleError(error)
          })
    })
    return{
      list,total
    }
  }
}
</script>

